import {ILogger} from '../../logger/LoggerInterface';
import LoggerFactory from '../../logger/LoggerFactory';
import Disposable from '../../lang/Disposable';
import FlashPlayer from './FlashPlayer';
import {StreamInfo} from './FlashRenderer';
import DisposableList from '../../lang/DisposableList';

export default class PhenixFlashVideo {
  private _logger: ILogger = LoggerFactory.getLogger('PhenixFlashVideo');
  private _ghost: HTMLVideoElement;
  private _stream: StreamInfo;
  private _options: {
    swfSrc: string[];
    streamId: string;
  };
  private _disposables: DisposableList = new DisposableList();
  private _flashPlayer: FlashPlayer;
  private _loaded: boolean;
  private _enabled: boolean;
  private _onReady: (enabled: boolean) => void;
  private _video: HTMLVideoElement;
  private _ghostInitStyleCssText: string;

  constructor(ghost, stream, options) {
    this._ghost = ghost;
    this._stream = stream;
    this._options = options || {};
    this._flashPlayer = null;

    const loaded = (success): void => {
      this._loaded = true;
      this._enabled = success === true;

      if (success) {
        this.initialize();
      } else {
        this._logger.error('Failed to create Phenix video element');
      }

      if (this._onReady) {
        this._onReady(this._enabled);
      }
    };

    try {
      this._video = this.createPhenixFlashVideoElement();

      this._video.className = this._ghost.className;
      this._video.height = this._ghost.height;
      this._video.width = this._ghost.width;

      this._ghostInitStyleCssText = this._ghost.style.cssText;
      this._ghost.style.cssText = 'visibility:hidden !important;width:0px !important;height:0px !important;' +
        'margin:0px !important;padding:0px !important;' +
        'border-style:none !important;border-width:0px !important;' +
        'max-width:0px !important;max-height:0px !important;outline:none !important';
      this._disposables.add(new Disposable(() => {
        this._ghost.style.cssText = this._ghostInitStyleCssText;
      }));
      /* eslint-disable */
      // @ts-ignore
      this._video.onunload = function() {
        this._loaded = false;
      };
      /* eslint-enable */

      this.observeInsertion();

      if (!document.body || !document.body.contains) {
        this._logger.info('document.body.contains is not supported');
      }

      if (document.body && document.body.contains && document.body.contains(this._ghost)) {
        this._ghost.parentNode.replaceChild(this._video, this._ghost);

        if (this._flashPlayer) {
          this._video = this._flashPlayer.finishInitializationInDom();
        }

        this._disposables.add(new Disposable(() => {
          if (this._video.parentNode) {
            this._video.parentNode.replaceChild(this._ghost, this._video);
          }
        }));
      }

      setTimeout(() => {
        loaded(true);
      });
    } catch (e) {
      this._logger.error('Error while loading Phenix RTC ' + e);
      loaded(false);
    }
  }

  hookUpEvents(): void {
    const ghost = this._ghost;
    const onError = (): void => {
      this.dispatchEvent(ghost, 'error');
    };

    const onMute = (): void => {
      ghost.muted = this._video.muted;
      this.dispatchEvent(ghost, 'mute');
    };

    const onUnmute = (): void => {
      ghost.muted = this._video.muted;
      this.dispatchEvent(ghost, 'unmute');
    };

    const onEnded = (): void => {
      /* eslint-disable */
      // @ts-ignore
      ghost.ended = this._video.ended;
      /* eslint-enable */
      this.dispatchEvent(ghost, 'ended');
    };

    const onLoadedMetadata = (): void => {
      ghost.width = this._video.width;
      ghost.height = this._video.height;
      this.dispatchEvent(ghost, 'loadedmetadata');
    };

    const onLoadedData = (): void => {
      ghost.width = this._video.width;
      ghost.height = this._video.height;
      this.dispatchEvent(ghost, 'loadeddata');
    };

    const onResize = (): void => {
      ghost.width = this._video.width;
      ghost.height = this._video.height;
      this.dispatchEvent(ghost, 'resize');
    };

    this.addEventListener('error', onError);
    this.addEventListener('mute', onMute);
    this.addEventListener('unmute', onUnmute);
    this.addEventListener('ended', onEnded);
    this.addEventListener('loadedmetadata', onLoadedMetadata);
    this.addEventListener('loadeddata', onLoadedData);
    this.addEventListener('resize', onResize);

    const eventDisposable = new Disposable(() => {
      this.removeEventListener('error', onError);
      this.removeEventListener('mute', onMute);
      this.removeEventListener('unmute', onUnmute);
      this.removeEventListener('ended', onEnded);
      this.removeEventListener('loadedmetadata', onLoadedMetadata);
      this.removeEventListener('loadeddata', onLoadedData);
      this.removeEventListener('resize', onResize);
    });

    this._disposables.add(eventDisposable);
  }

  getElement(): HTMLVideoElement {
    return this._video;
  }

  onReady(callback): void {
    if (this._loaded) {
      setTimeout(() => {
        callback(this._enabled);
      }, 1);
    } else {
      this._onReady = callback;
    }
  }

  dispose(): void{
    this._disposables.dispose();
  }

  private addEventListener(name, listener /* , useCapture */): void {
    window.addEventListener(name, listener);
  }

  private removeEventListener(name, listener /* , useCapture */): void {
    window.removeEventListener(name, listener);
  }

  private dispatchEvent(source, name): void {
    let event;

    if (document.createEvent) {
      event = document.createEvent('HTMLEvents');
      event.initEvent(name, true, true);
    } else {
      /* eslint-disable */
      // @ts-ignore
      event = document.createEventObject();
      event.eventType = name;
      /* eslint-enable */
    }

    event.eventName = name;

    setTimeout(() => {
      if (document.createEvent) {
        source.dispatchEvent(event);
      } else {
        source.fireEvent('on' + event.eventType, event);
      }
    });
  }

  private createPhenixFlashVideoElement(): HTMLVideoElement {
    this._flashPlayer = new FlashPlayer(this._ghost, this._stream, this._options);

    this._disposables.add(new Disposable(() => {
      this._flashPlayer.dispose();
    }));

    return this._flashPlayer.getElement();
  }

  private observeInsertion(): void {
    if (window['MutationObserver']) {
      // Newer browsers support an efficient way to observe DOM modifications
      const observer = new MutationObserver(mutations => {
        mutations.forEach(mutation => {
          if (mutation.type === 'childList') {
            for (let i = 0; i < mutation.addedNodes.length; i++) {
              const node = mutation.addedNodes[i];

              if (mutation.target !== this._video) {
                if (node === this._ghost) {
                  // Replace element with our video element
                  mutation.target.replaceChild(this._video, this._ghost);
                  this.initialize();
                } else if (this.isDescendant(mutation.target, this._ghost)) {
                  if (this._ghost.parentNode) {
                    this._ghost.parentNode.replaceChild(this._video, this._ghost);
                  }

                  this.initialize();
                }
              }
            }
          }
        });
      });
      const configMutations = {
        childList: true,
        attributes: false,
        characterData: false,
        subtree: true
      };

      observer.observe(document.body, configMutations);

      this._disposables.add(new Disposable(() => {
        observer.disconnect();
      }));
    } else {
      // For older browsers. There is a significant performance overhead with MutationObserver.
      // See https://developer.mozilla.org/en-US/docs/Web/Guide/Events/Mutation_events
      this._logger.info('Falling back to use of DOM event listeners. This results in degraded performance for further DOM modifications and does not work for IE prior to version 9. See https://developer.mozilla.org/en-US/docs/Web/Guide/Events/Mutation_events for details.');

      const domInsertedListener = (): void => {
        if (this._ghost.parentNode) {
          this._ghost.parentNode.replaceChild(this._video, this._ghost);
        }
      };

      this.addEventListener('DOMNodeInserted', domInsertedListener);

      this._disposables.add(new Disposable(() => {
        this.removeEventListener('DOMNodeInserted', domInsertedListener);
      }));
    }
  }

  initialize(): void {
    this.hookUpEvents();

    this.propagateAttributeChanges();

    this._video.id = this._ghost.id || this._video.id;
    this._video.style.cssText = this._ghostInitStyleCssText;
    this._video.className = this._ghost.className;
    /* eslint-disable */
    // @ts-ignore
    this._video.innerHtml = this._ghost.innerHTML;
    /* eslint-enable */
    this._video.width = this._ghost.width;
    this._video.height = this._ghost.height;
    this._video.autoplay = this._ghost.autoplay;
    this._video.muted = this._ghost.muted;
    this._video.defaultMuted = this._ghost.defaultMuted;
    this._video.volume = this._ghost.volume;
  }

  private isDescendant(parent, child): boolean {
    let node = child.parentNode;

    while (node !== null) {
      if (node === parent) {
        return true;
      }

      node = node.parentNode;
    }

    return false;
  }

  propagateAttributeChanges(): void {
    const readonly = ['style'];

    if (window['MutationObserver']) {
      // Newer browsers support an efficient way to observe DOM modifications
      const observer = new MutationObserver(mutations => {
        mutations.forEach(mutation => {
          if (mutation.type === 'attributes' && mutation.target === this._ghost && readonly.indexOf(mutation.attributeName) === -1) {
            this._video[mutation.attributeName] = this._ghost[mutation.attributeName];
          }
        });
      });
      const configAttributes = {attributes: true};

      observer.observe(this._ghost, configAttributes);

      this._disposables.add(new Disposable(() => {
        observer.disconnect();
      }));
    } else {
      // For older browsers. There is a significant performance overhead with MutationObserver.
      // See https://developer.mozilla.org/en-US/docs/Web/Guide/Events/Mutation_events
      this._logger.info('Falling back to use of DOM event listeners. This results in degraded performance for further DOM modifications and does not work for IE prior to version 9. See https://developer.mozilla.org/en-US/docs/Web/Guide/Events/Mutation_events for details.');

      const handleModifiedEvent = (event): void => {
        this._video[event.target.tagName] = this._ghost[event.target.tagName];
      };

      if (this._ghost.addEventListener) {
        this._ghost.addEventListener('DOMAttrModified', handleModifiedEvent, false);

        this._disposables.add(new Disposable(() => {
          this._ghost.removeEventListener('DOMAttrModified', handleModifiedEvent, false);
        }));
      } else {
        /* eslint-disable */
        // @ts-ignore
        this._ghost.attachEvent('onpropertychange', handleModifiedEvent);

        this._disposables.add(new Disposable(() => {
          // @ts-ignore
          this._ghost.detachEvent('DOMAttrModified', handleModifiedEvent);
        }));
        /* eslint-enable */
      }
    }
  }
}