import {ILogger} from '../../logger/LoggerInterface';
import LoggerFactory from '../../logger/LoggerFactory';
import PhenixFlashVideo from './PhenixFlashVideo';
import {LiveStreamingOptions, LiveStreamingStatistics} from './StreamTypes';
import {NetworkStates} from './NetworkStates';

const timeoutForStallWithoutProgressToRestart = 6000;
const minTimeBeforeNextReload = 15000;
const mostRecentSwfFile = 'rtmp-flash-renderer-2019.2.18.swf';
const defaultSwfFileSrcs = {
  local: 'https://local.phenixrts.com/public/rtmp/' + mostRecentSwfFile,
  staging: 'https://stg.phenixrts.com/public/rtmp/' + mostRecentSwfFile,
  production: 'https://phenixrts.com/public/rtmp/' + mostRecentSwfFile
};

export type StreamInfo = {
  uri: string;
  bitrate: string;
  resolution: string;
};

export default class FlashRenderer {
  private readonly _logger: ILogger = LoggerFactory.getLogger('FlashPlayer');
  private readonly _stopPlayer: () => void;
  private readonly _onStalled: () => void;
  private readonly _onEnded: () => void;
  private readonly _streamId: string;
  private readonly _streamsInfo: StreamInfo[];
  private readonly _swfSrc: string;

  private _options: LiveStreamingOptions;
  private _phenixVideo: PhenixFlashVideo;
  private _playerElement: HTMLVideoElement;
  private _originElement: HTMLVideoElement;
  private _player: {getStats(): LiveStreamingStatistics};
  private _hasFlashPlugin: boolean;
  private _lastReloadTime: number;

  constructor(videoElement, type, streamId, streamsInfo, options, stopPlayer: () => void) {
    const defaultSwfFileSrc = defaultSwfFileSrcs[options.env || 'staging'];

    this._streamId = streamId;
    this._streamsInfo = streamsInfo.map(info => {
      info.uri = encodeURI(info.uri).replace(/[#]/g, '%23');

      return info;
    });
    this._options = options;
    this._phenixVideo = null;
    this._playerElement = null;
    this._player = null;
    this._originElement = videoElement;

    this._onStalled = this.stalled.bind(this);
    this._onEnded = this.ended.bind(this);
    this._swfSrc = options.swfSrc || defaultSwfFileSrc;
    this._stopPlayer = stopPlayer;
  }

  isSupported(): boolean {
    return this.detectFlashPlugin();
  }

  start(): HTMLVideoElement {
    const options = {
      streamId: this._streamId,
      swfSrc: this._swfSrc
    };

    this._phenixVideo = new PhenixFlashVideo(this._originElement, this._streamsInfo, options);

    this._phenixVideo.hookUpEvents();

    this._playerElement = this._phenixVideo.getElement();

    if (this._playerElement) {
      this._playerElement.play();
    }

    this._playerElement.addEventListener('stalled', this._onStalled, false);
    this._playerElement.addEventListener('pause', this._onStalled, false);
    this._playerElement.addEventListener('suspend', this._onStalled, false);
    this._playerElement.addEventListener('ended', this._onEnded, false);

    return this._playerElement;
  }

  dispose(): void {
    if (this._phenixVideo) {
      const finalizeStreamEnded = (): void => {
        if (this._playerElement) {
          this._playerElement.removeEventListener('stalled', this._onStalled, false);
          this._playerElement.removeEventListener('pause', this._onStalled, false);
          this._playerElement.removeEventListener('suspend', this._onStalled, false);
          this._playerElement.removeEventListener('ended', this._onEnded, false);
        }

        this._playerElement = null;
        this._originElement = null;
      };

      try {
        this._phenixVideo.dispose();
        this._phenixVideo = null;

        finalizeStreamEnded();

        this._logger.info('[%s] Flash player has been destroyed', this._streamId);
      } catch (e) {
        this._logger.error('[%s] Error while destroying Flash player [%s]', this._streamId, e.code, e);

        finalizeStreamEnded();
      }
    }
  }

  getStats(): LiveStreamingStatistics {
    const statistics = {
      width: 0,
      height: 0,
      currentTime: 0.0,
      lag: 0.0,
      networkState: NetworkStates.NetworkNoSource
    };

    if (!this._playerElement) {
      return statistics;
    }

    statistics.currentTime = this._playerElement.currentTime;

    const trueCurrentTime = (Date.now() - this._options.originStartTime) / 1000;

    statistics.lag = Math.max(0.0, trueCurrentTime - statistics.currentTime);

    return statistics;
  }

  getPlayer(): HTMLVideoElement {
    return this._playerElement;
  }

  reload(): void {
    this._phenixVideo.dispose();

    this.start();
  }

  private detectFlashPlugin(): boolean {
    const defaultVersion = [10, 0, 0];
    const pluginName = 'Shockwave Flash';
    const mimeType = 'application/x-shockwave-flash';
    const activeX = 'ShockwaveFlash.ShockwaveFlash';
    let version: string[] | number[] = ['0', '0', '0'];

    if (typeof this._hasFlashPlugin === 'boolean') {
      return this._hasFlashPlugin;
    }

    // Firefox, Webkit, Opera
    if (window.navigator && window.navigator.plugins && window.navigator.plugins[pluginName]) {
      const description = window.navigator.plugins[pluginName].description;

      /* eslint-disable */
      if (description &&
        window.navigator.mimeTypes &&
        window.navigator.mimeTypes[mimeType] &&
        !window.navigator.mimeTypes[mimeType].enabledPlugin) {
        version = description.replace(pluginName, '').replace(/^\s+/, '').replace(/\sr/gi, '.').split('.');

        for (let i = 0, total = version.length; i < total; i++) {
          // @ts-ignore
          version[i] = parseInt(version[i].match(/\d+/), 10);
        }
      }
      // Internet Explorer / ActiveX
      // @ts-ignore
    } else if (window.ActiveXObject) {
      try {
        // @ts-ignore
        const ax = new window.ActiveXObject(activeX);

        if (ax) {
          const versionString = ax.GetVariable('$version') || '';
          const versionInfo = versionString.split(' ')[1].version.split(',');

          version = [parseInt(versionInfo[0], 10), parseInt(versionInfo[1], 10), parseInt(versionInfo[2], 10)];
        }
      } catch (e) {
        console.error(e);
      }
    }
    /* eslint-enable */

    this._hasFlashPlugin = (version[0] > defaultVersion[0] || (version[0] === defaultVersion[0] && version[1] > 0) || (version[0] === defaultVersion[0] && version[1] === 0 && version[2] >= 0));

    return this._hasFlashPlugin;
  }

  private stalled(): void {
    this._logger.info('[%s] Loading flash player stalled caused by [%s] event.', this._streamId, event.type);

    const currentVideoTime = this._playerElement.currentTime;

    setTimeout(() => {
      if (this._playerElement && this._playerElement.currentTime === currentVideoTime && !this._playerElement.paused && this.canReload()) {
        this._logger.warn('Reloading stream after being stalled for [%s] seconds', timeoutForStallWithoutProgressToRestart / 1000);

        this.reloadIfAble();
      }
    }, timeoutForStallWithoutProgressToRestart);
  }

  private ended(): void {
    this._logger.info('[%s] Flash player ended.', this._streamId);
    this._stopPlayer();
  }

  private canReload(): boolean {
    const hasElapsedMinTimeSinceLastReload = !this._lastReloadTime || Date.now() - this._lastReloadTime > minTimeBeforeNextReload;

    return this._playerElement && this._playerElement.buffered.length !== 0 && hasElapsedMinTimeSinceLastReload;
  }

  private reloadIfAble(): void {
    if (!this.canReload()) {
      return;
    }

    this._logger.warn('Reloading unhealthy stream that was active for at least [%s] seconds', minTimeBeforeNextReload / 1000);

    this._lastReloadTime = Date.now();

    this.reload();
  }
}