import Disposable from '../../lang/Disposable';
import BrowserDetector from '../../dom/BrowserDetector';
import {ILogger} from '../../logger/LoggerInterface';
import LoggerFactory from '../../logger/LoggerFactory';
import Strings from '../../lang/Strings';
import {StreamInfo} from './FlashRenderer';
import DisposableList from '../../lang/DisposableList';

const defaultPropertyValues = {
  width: 480,
  height: 360,
  videoWidth: 480,
  videoHeight: 360,
  muted: false,
  volume: 1,
  currentTime: 0,
  duration: Infinity,
  readyState: 0,
  ended: false,
  buffered: null,
  autoplay: true
};
const mutedElementListenerInterval = 300;
const isEdgeAndRequiresFlashObject = BrowserDetector.browserName === 'Edge';
const missingFlashMessage = 'You are using a browser this does not have Flash player enabled or installed. Please turn on your Flash player plugin or download the latest version from https://get.adobe.com/flashplayer/';

type EventDisposable = {
  name: string;
  listener: () => void;
  disposable: Disposable;
}

export default class FlashPlayer {
  private _logger: ILogger = LoggerFactory.getLogger('FlashPlayer');
  private _isVideo: boolean;
  private _swfSrc: string;
  private _id: string;
  private _width: number;
  private _height: number;
  private _ghost: HTMLVideoElement;
  private _element: HTMLVideoElement;
  private _eventDisposables: EventDisposable[];
  private _disposables: DisposableList = new DisposableList();
  private _flashVars: string[];
  private _isFullscreen: boolean;

  constructor(ghost, streamInfo: StreamInfo, options) {
    this._isVideo = ghost.tagName === 'video';
    this._swfSrc = options.swfSrc;
    this._id = ghost.id || options.streamId;
    this._width = ghost.clientWidth;
    this._height = ghost.clientHeight;
    this._ghost = ghost;
    this._element = null;
    this._eventDisposables = [];
    this._flashVars = [
      'uid=' + this._id,
      `autoplay=${!!ghost.autoplay}`,
      `muted=${!!ghost.muted}`,
      'allowScriptAccess=always',
      'preload=true',
      `src=${JSON.stringify(streamInfo)}`
    ];

    this._disposables.add(new Disposable(() => {
      this.triggerFlashEvent('fire_stop');
    }));

    try {
      if (isEdgeAndRequiresFlashObject) {
        this._element = this.createMicrosoftFlashElement();
      } else {
        this._element = this.setupCrossBrowserFlashElement();
      }

      this.setupElement();
    } catch (e) {
      this._logger.error('Error while loading Flash Player ' + e);
    }
  }

  get element(): HTMLVideoElement {
    return this._element;
  }

  addEventListener(name, listener): void {
    const eventDisposableIndex = this.findEventListenerIndex(name, listener);

    if (eventDisposableIndex > -1) {
      return;
    }

    document.addEventListener(name, listener);

    const disposable = new Disposable(() => {
      document.removeEventListener(name, listener);
    });

    this._disposables.add(disposable);

    this._eventDisposables.push({
      name: name,
      listener: listener,
      disposable: disposable
    });
  }

  removeEventListener(name, listener): void {
    const eventDisposableIndex = this.findEventListenerIndex(name, listener);

    if (eventDisposableIndex > -1) {
      const eventDisposable = this._eventDisposables.splice(eventDisposableIndex, 1)[0];

      eventDisposable.disposable.dispose();
    }
  }

  triggerFlashEvent(eventName, args?): () => void {
    const triggerEvent = this._element[eventName];

    if (triggerEvent) {
      return triggerEvent.apply(this._element, args);
    }
  }

  dispose(): void {
    this._disposables.dispose();
  }

  createMicrosoftFlashElement(): HTMLVideoElement {
    return document.createElement('div') as unknown as HTMLVideoElement;
  }

  setupCrossBrowserFlashElement(): HTMLVideoElement {
    const element = document.createElement('embed') as unknown as HTMLVideoElement;

    element.setAttribute('id', this._id);
    element.setAttribute('name', '__' + this._id);
    element.setAttribute('play', 'true');
    element.setAttribute('loop', 'false');
    element.setAttribute('quality', 'high');
    element.setAttribute('bgcolor', '#000000');
    element.setAttribute('wmode', 'transparent');
    element.setAttribute('allowScriptAccess', 'always');
    element.setAttribute('allowFullScreen', 'true');
    element.setAttribute('type', 'application/x-shockwave-flash');
    element.setAttribute('pluginspage', '//www.macromedia.com/go/getflashplayer');
    element.setAttribute('src', this._swfSrc);
    element.setAttribute('flashvars', this._flashVars.join('&'));

    if (this._isVideo) {
      element.setAttribute('width', this._width.toString());
      element.setAttribute('height', this._height.toString());
    }

    return element;
  }

  createParameterElement(name: string, value: string): HTMLParamElement {
    const parameter = document.createElement('param');

    parameter.setAttribute('name', name);
    parameter.setAttribute('value', value);

    return parameter;
  }

  setupElement(): void {
    this._element = Object.assign(this._element, defaultPropertyValues, {
      id: this._id,
      width: this._width,
      height: this._height,
      videoWidth: this._width,
      videoHeight: this._height
    });

    this.setupAccessors();
    this.propagateAttributeChanges();
    this.setupVolumeAndMutedListeners();
  }

  createEvent(eventName, target): Event {
    const eventFrags = eventName.match(/([a-z]+\.([a-z]+))/i);
    const detail = {
      target: target,
      namespace: ''
    };

    if (eventFrags !== null) {
      eventName = eventFrags[1];
      detail.namespace = eventFrags[2];
    }

    if (BrowserDetector.browserName === 'IE' && BrowserDetector.browserMajorVersion > 8) {
      const customEvent = window.document.createEvent('CustomEvent');

      customEvent.initCustomEvent(eventName, false, false, detail);

      return customEvent;
    }

    if (!window.CustomEvent) {
      return detail as unknown as Event;
    }

    return new window.CustomEvent(eventName, {detail: detail});
  }

  getValueFromFlash(name): string {
    const defaultGetter = (): void => null;
    const getter = this._element['get_' + name] || defaultGetter;

    return getter.call(this._element);
  }

  setupAccessors(): void {
    /* eslint-disable */
    window['__event__' + this._id] = (eventName, message) => {

      const event = this.createEvent(eventName, this._element);

      if (message) {
        try {
          // @ts-ignore
          event.data = JSON.parse(message);
          // @ts-ignore
          event.details.data = event.data;
        } catch (e) {
          // @ts-ignore
          event.message = message;
        }
      }

      switch (eventName) {
        case 'ended':
          // @ts-ignore
          this._element.ended = true;

          this._element.dispatchEvent(event);

          break;
        case 'error':
          // @ts-ignore
          this._element.error = event.data;

          if (this._element.dispatchEvent) {
            // @ts-ignore
            this._element.dispatchEvent(new Error(message));
          }

          break;
        case 'loadeddata':
        case 'loadedmetadata':
        case 'loadstart':
          if (this._element.dispatchEvent) {
            this._element.dispatchEvent(event);
          }

          break;
        case 'pause':
          // @ts-ignore
          this._element.paused = true;

          break;
        case 'play':
          // @ts-ignore
          this._element.paused = false;

          break;
        case 'playing':
          // @ts-ignore
          this._element.paused = false;

          break;
        case 'progress':
        case 'seeked':
        case 'seeking':
        case 'stalled':
        case 'suspend':
          if (this._element.dispatchEvent) {
            this._element.dispatchEvent(event);
          }

          break;
        case 'timeupdate':
          // @ts-ignore
          this._element.currentTime = this.getValueFromFlash('currentTime');
          if (this._element.dispatchEvent) {
            this._element.dispatchEvent(event);
          }

          break;
        case 'volumechange':
          break;
        case 'waiting':
          break;
        default:
          break;
      }

      this._element.dispatchEvent(event);
      /* eslint-enable */
    };
    /* eslint-disable */
    // @ts-ignore
    const nativeFullScreen = this._element.requestFullscreen || this._element.mozRequestFullScreen || this._element.webkitRequestFullScreen || this._element.msRequestFullscreen;

    this._disposables.add(new Disposable(() => {
      delete window['__event__' + this._id];
    }));
    // @ts-ignore
    this._element.play = () => {
      this.triggerFlashEvent('fire_play');
    };
    // @ts-ignore
    this._element.load = () => {
      this.triggerFlashEvent('fire_load');
    };
    // @ts-ignore
    this._element.seek = () => {
      this.triggerFlashEvent('fire_seek');
    };
    // @ts-ignore
    this._element.pause = () => {
      this.triggerFlashEvent('fire_pause');
    };
    // @ts-ignore
    this._element.requestFullscreen = function() {
      this._isFullscreen = true;
      this.addListenerForExitFullScreen();

      this._previousHeight = this._element.style.height;
      this._previousWidth = this._element.style.width;
      this._previousPosition = this._element.style.position;
      this._element.style.position = 'absolute';
      this._element.style.height = '100%';
      this._element.style.width = '100%';
      /* eslint-disable */
      // @ts-ignore
      if (typeof window.ActiveXObject !== 'undefined') { // Older IE.
        // @ts-ignore
        const wscript = new window.ActiveXObject('WScript.Shell');

        if (wscript !== null) {
          wscript.SendKeys('{F11}');
        }
      }
      /* eslint-enable */

      nativeFullScreen.call(this._element);
    };

    const triggerFlashSetSize = (): void => {
      this.triggerFlashEvent('set_size', [this._isFullscreen ? screen.width : this._element.clientWidth, this._isFullscreen ? screen.height : this._element.clientHeight]);
    };

    window.addEventListener('resize', triggerFlashSetSize);

    this._disposables.add(new Disposable(() => {
      window.removeEventListener('resize', triggerFlashSetSize);
    }));
  }

  finishInitializationInDom(): HTMLVideoElement {
    if (!isEdgeAndRequiresFlashObject) {
      return this._element;
    }

    const id = (isEdgeAndRequiresFlashObject ? '__' + this._id : this._id) + Strings.random(32);
    const embedObject = document.createElement('object') as HTMLDivElement;
    const missingFlashMessageElement = document.createElement('div');
    const childrenElements = [
      this.createParameterElement('movie', this._swfSrc + '?x=' + Date.now()),
      this.createParameterElement('flashvars', this._flashVars.join('&')),
      this.createParameterElement('quality', '2'),
      this.createParameterElement('bgcolor', '#000000'),
      this.createParameterElement('wmode', 'transparent'),
      this.createParameterElement('allowScriptAccess', 'always'),
      this.createParameterElement('allowFullScreen', 'true'),
      missingFlashMessageElement
    ];

    missingFlashMessageElement.innerHTML = missingFlashMessage;

    if (isEdgeAndRequiresFlashObject) {
      embedObject.setAttribute('type', 'application/x-shockwave-flash');
      embedObject.setAttribute('data', this._swfSrc);
      embedObject.setAttribute('id', id);
      embedObject.setAttribute('width', '__' + this._width.toString());
      embedObject.setAttribute('height', '__' + this._height.toString());
    } else {
      embedObject.setAttribute('classid', 'clsid:D27CDB6E-AE6D-11cf-96B8-444553540000');
      embedObject.setAttribute('codebase', '//download.macromedia.com/pub/shockwave/cabs/flash/swflash.cab');
      embedObject.setAttribute('id', id);
      embedObject.setAttribute('width', this._width.toString());
      embedObject.setAttribute('height', this._height.toString());
    }
    /* eslint-disable */
    if ('object' in embedObject) {
      childrenElements.forEach((element) => {
        // @ts-ignore
        if (!embedObject.object[element.name]) {
          if ('value' in element) {
            // @ts-ignore
            embedObject.object[element.name] = element.value;
          }
        }
      });

      // @ts-ignore
      embedObject.appendChild(missingFlashMessageElement);
    } else {
      childrenElements.forEach((element) => {
        // @ts-ignore
        const existingChildElement = embedObject.children.find((childElement) => {
          return 'name' in element ? childElement.name === element.name : false;
        });

        if (existingChildElement) {
          return embedObject.replaceChild(element, existingChildElement);
        }

        return embedObject.appendChild(element);
      });
      /* eslint-enable */
    }

    this._element.outerHTML = embedObject.outerHTML;

    // Issue with IE. When you replace the outerHTML the reference to this element does not correctly reflect the changes.
    // If you want to get the new embed object in the dom you need to query for it.
    if (window.document.getElementById) {
      /* eslint-disable */
      // @ts-ignore
      this._element = window.document.getElementById(id);
      /* eslint-enable */

      this.setupElement();
    }

    return this._element;
  }

  propagateAttributeChanges(): void {
    const ignored = ['currentTime', 'paused', 'error', 'src'];

    if (window['MutationObserver']) {
      // Newer browsers support an efficient way to observe DOM modifications
      const observer = new MutationObserver(mutations => {
        mutations.forEach(mutation => {
          if (mutation.type === 'attributes' && mutation.target === this._element && !ignored.includes(mutation.attributeName)) {
            this.setFlashValue(mutation.attributeName, this._element[mutation.attributeName]);
          }
        });
      });
      const configAttributes = {attributes: true};

      observer.observe(this._element, configAttributes);
      this._disposables.add(new Disposable(() => {
        observer.disconnect();
      }));
    } else {
      // For older browsers. There is a significant performance overhead with MutationObserver.
      // See https://developer.mozilla.org/en-US/docs/Web/Guide/Events/Mutation_events
      this._logger.info('Falling back to use of DOM event listeners. This results in degraded performance for further DOM modifications and does not work for IE prior to version 9. See https://developer.mozilla.org/en-US/docs/Web/Guide/Events/Mutation_events for details.');

      const handleModifiedEvent = (event): void => {
        if (!ignored.includes(event.target.tagName)) {
          this.setFlashValue(event.target.tagName, this._element[event.target.tagName]);
        }
      };

      if (this._element.addEventListener) {
        this._element.addEventListener('DOMAttrModified', handleModifiedEvent, false);

        this._disposables.add(new Disposable(() => {
          this._element.removeEventListener('DOMAttrModified', handleModifiedEvent, false);
        }));
      } else {
        /* eslint-disable */
        // @ts-ignore
        this._element.attachEvent('onpropertychange', handleModifiedEvent);
        this._disposables.add(new Disposable(() => {
          // @ts-ignore
          this._element.detachEvent('DOMAttrModified', handleModifiedEvent);
        }));
        /* eslint-enable */
      }
    }
  }

  getElement(): HTMLVideoElement {
    return this._element;
  }

  setFlashValue(name: string, value: string | boolean | number): () => void {
    const defaultSetter = (): void => null;
    const setter = this._element['set_' + name] || defaultSetter;

    return setter.call(this._element, value);
  }

  setupVolumeAndMutedListeners(): void {
    const mutedListener = this.getMutedListener();
    const volumeListener = this.getVolumeListener();
    const intervalId = setInterval(() => {
      mutedListener();
      volumeListener();
    }, mutedElementListenerInterval);

    this._disposables.add(new Disposable(() => {
      clearInterval(intervalId);
    }));
  }

  getMutedListener(): () => void {
    if (!this._ghost || !this._element) {
      return (): void => null;
    }

    let ghostLastMutedState = this._ghost.muted;
    let elementLastMutedState = this._element.muted;

    return (): void => {
      const newGhostMutedState = this._element.muted;
      let newElementMutedState = this._element.muted;

      if (newGhostMutedState === ghostLastMutedState && newElementMutedState === elementLastMutedState) {
        return;
      }

      if (newGhostMutedState !== ghostLastMutedState) {
        ghostLastMutedState = newGhostMutedState;
        elementLastMutedState = newGhostMutedState;
        newElementMutedState = newGhostMutedState;
      }

      if (newElementMutedState !== elementLastMutedState) {
        ghostLastMutedState = newGhostMutedState;
        elementLastMutedState = newGhostMutedState;
      }

      this._element.muted = newElementMutedState;
      this._ghost.muted = newElementMutedState;

      this.setFlashValue('muted', newElementMutedState);
    };
  }

  getVolumeListener(): () => void {
    if (!this._ghost || !this._element) {
      return (): void => null;
    }

    let ghostLastVolume = this._ghost.volume;
    let elementLastVolume = this._element.volume;

    return (): void => {
      const newGhostVolume = this._element.volume;
      let newElementVolume = this._element.volume;

      if (newGhostVolume === ghostLastVolume && newElementVolume === elementLastVolume) {
        return;
      }

      if (newGhostVolume !== ghostLastVolume) {
        ghostLastVolume = newGhostVolume;
        elementLastVolume = newGhostVolume;
        newElementVolume = newGhostVolume;
      }

      if (newElementVolume !== elementLastVolume) {
        ghostLastVolume = newGhostVolume;
        elementLastVolume = newGhostVolume;
      }

      this._element.volume = newElementVolume;
      this._ghost.volume = newElementVolume;

      this.setFlashValue('volume', newElementVolume);
    };
  }

  findEventListenerIndex(name, listener): number {
    return this._eventDisposables.findIndex(eventDisposable => {
      return eventDisposable.name === name && eventDisposable.listener === listener;
    });
  }
}